.elastic-ip {
  padding: 0.26rem 0.3rem;
}
.elastic-ip .top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 0.2rem;
}
.elastic-ip .top .tip {
  color: #9696A3;
  padding: 0.08rem 0;
  line-height: 1;
}
.elastic-ip .create {
  line-height: 0.4rem;
  padding: 0 0.16rem;
  color: #fff;
  background: #5C5EE4;
  border-radius: 0.04rem;
  cursor: pointer;
}
.elastic-ip table {
  border: 1px solid #d9dbe1 !important;
}
.elastic-ip .ant-table-tbody > tr > td:first-child,
.elastic-ip .ant-table-thead > tr > th:first-child {
  padding-left: 0.6rem;
}
/* refund-modal */
.refund-modal {
  width: 6.4rem !important;
  font-family: 'Source Han Sans CN';
}
.refund-modal .ant-modal-body {
  padding: 0.4rem 0.3rem;
}
.refund-modal .modal-title {
  font-size: 0.18rem;
  color: #040E34;
  line-height: 1;
  margin-bottom: 0.3rem;
}
.refund-modal .config-item {
  margin-bottom: 0.2rem;
  display: flex;
}
.refund-modal .config-item:first-child .row-r {
  padding: 0.25rem 0.2rem;
  font-size: 0.13rem;
  color: #9696A3;
}
.refund-modal .config-item:first-child .l-tit {
  margin-right: 0.2rem;
}
.refund-modal .config-item:first-child .item {
  display: flex;
  line-height: 0.14rem;
  margin-bottom: 0.2rem;
}
.refund-modal .config-item:first-child .item:last-child {
  margin-bottom: 0;
}
.refund-modal .config-item:first-child .item p {
  margin-bottom: 0;
}
.refund-modal .config-item:first-child .r-text p {
  margin-bottom: 0.1rem;
}
.refund-modal .config-item:first-child .r-text p:last-child {
  margin-bottom: 0;
}
.refund-modal .row-l {
  line-height: 0.4rem;
  font-size: 0.14rem;
  color: #040E34;
  min-width: 0.8rem;
}
.refund-modal .row-r {
  border-radius: 0.04rem;
  background: #F2F2F7;
  flex: 1;
}
.refund-modal textarea,
.refund-modal .ant-select-selector {
  background: #F2F2F7 !important;
  resize: none;
  border-color: #F2F2F7 !important;
}
.refund-modal .money {
  align-items: center;
}
.refund-modal .money .row-l,
.refund-modal .money .row-r {
  line-height: 1;
}
.refund-modal .modal-footer {
  text-align: center;
}
.refund-modal .modal-footer .ant-btn {
  line-height: 0.4rem;
  height: 0.4rem;
  min-width: 0.8rem;
  padding: 0 0.16rem;
  border-radius: 0.04rem;
  margin: 0 0.04rem;
}
.refund-modal .modal-footer .sure {
  background: #5C5EE4;
  color: #fff;
  border-color: #5C5EE4;
}
.refund-modal .modal-footer .cancel {
  border-color: #D9DBE1;
  color: #525666;
}
.refund-modal .ant-select-selection-item {
  font-size: 0.14rem;
  color: #040E34;
}
/* create-ip */
.create-ip {
  /* total-box */
}
.create-ip .com-tit .tit {
  color: #040E34;
  line-height: 1;
  margin-bottom: 0.32rem;
  font-size: 0.18rem;
}
.create-ip .bw_size label::before {
  display: inline-block;
  margin-right: 4px;
  color: #ff4d4f;
  font-size: 14px;
  font-family: SimSun, sans-serif;
  line-height: 1;
  content: '*';
}
.create-ip .com-card {
  padding: 0.56rem 0.3rem 1.45rem 0.3rem;
}
.create-ip .ant-select {
  width: 3.9rem;
  line-height: 0.44rem;
  height: 0.44rem;
}
.create-ip .ant-select-selector,
.create-ip .ant-select-selection-item {
  height: 0.44rem !important;
  line-height: 0.44rem !important;
  border-radius: 0.04rem !important;
}
.create-ip .s-tip {
  font-size: 0.14rem;
  color: #9696A3;
  line-height: 1;
  margin-top: 0.1rem;
}
.create-ip #ipForm .ant-slider-rail {
  background: #E7E7E7;
}
.create-ip #ipForm .ant-slider-track {
  background: #5C5EE4;
}
.create-ip #ipForm .ant-slider-handle {
  border-color: #5C5EE4;
}
.create-ip #ipForm .ant-slider-dot {
  width: 1px;
  height: 5px;
  border-radius: 0;
  background: #ddd;
  top: 4px;
  border: 0;
}
.create-ip #ipForm .ant-slider-dot:first-child {
  left: 4px !important;
}
.create-ip #ipForm .ant-slider-dot:last-child {
  left: auto !important;
  right: 4px !important;
}
.create-ip #vpc_package_id {
  margin-bottom: -0.3rem;
}
.create-ip .ant-col .item {
  border: 1px solid #D9DBE1;
  border-radius: 0.04rem;
  padding: 0.09rem 0.16rem;
  line-height: 0.28rem;
  position: relative;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.14rem;
  color: #040E34;
  margin-bottom: 0.3rem;
}
.create-ip .ant-slider {
  width: 4.5rem;
}
.create-ip .c-active {
  background: #fff !important;
  box-shadow: 0 0 0.2rem 0.05rem rgba(137, 138, 228, 0.3);
}
.create-ip .c-active.c-hover {
  border-color: transparent !important;
}
.create-ip .c-active::after {
  content: '';
  width: 0.2rem;
  height: 0.2rem;
  position: absolute;
  text-align: center;
  line-height: 0.2rem;
  top: 0;
  right: 0;
  z-index: 2;
  background: url(../../../../assets/images/cloud/check.png) no-repeat;
  background-size: 100%;
}
.create-ip .network .item {
  display: flex;
  justify-content: center;
  font-size: 0.18rem;
  color: #040E34;
}
.create-ip .bw_size {
  font-size: 0.14rem;
  color: #040E34;
}
.create-ip .bw_size .ant-input-group-compact {
  display: flex;
  align-items: center;
}
.create-ip .bw_size .ant-input-group-compact .ant-input,
.create-ip .bw_size .ant-input-group-compact .ant-input-number {
  width: 0.96rem;
  line-height: 0.48rem;
  margin-left: 0.3rem;
  margin-right: 0.1rem;
  border-radius: 0.04rem;
}
.create-ip .total-box {
  background: #fff;
  z-index: 20;
  width: 100%;
  height: 1.45rem;
  position: fixed;
  bottom: 0;
  left: 0;
  padding-left: 265px;
  box-shadow: 0 0 0.2rem 0.05rem rgba(137, 138, 228, 0.3);
}
.create-ip .total-box .discount-list {
  font-size: 0.12rem;
  color: #9696A3;
  height: 0.5rem;
  overflow-y: auto;
}
.create-ip .total-box .discount-list .num {
  font-size: 0.14rem;
  color: #FCAF55;
}
.create-ip .total-box .discount-list .item {
  margin-bottom: 0.05rem;
  line-height: 1;
}
.create-ip .total-box .discount-list .anticon {
  margin-left: 0.1rem;
  cursor: pointer;
}
.create-ip .total-box #bot .ant-popover {
  padding: 0.12rem 0.17rem;
  background: #fff;
}
.create-ip .total-box #bot .ant-popover .discount .ant-input {
  width: 1.8rem;
  line-height: 0.4rem;
  height: 0.4rem;
  display: inline-block;
  background: #F2F2F7;
  border-radius: 0.04rem;
}
.create-ip .total-box #bot .ant-popover .discount .close {
  line-height: 0.4rem;
  padding: 0 0.16rem;
  border-radius: 0.04rem;
  background: #FEB63B;
  font-size: 0.14rem;
  color: #fff;
  margin-left: 0.08rem;
  display: inline-block;
}
.create-ip .total-box .discount_code {
  display: inline-block;
  vertical-align: top;
  top: 0.3rem;
  position: relative;
  cursor: pointer;
}
.create-ip .total-box .r-btn {
  display: inline-block;
  margin-left: 0.06rem;
}
.create-ip .total-box .checkout {
  width: 1.6rem;
  line-height: 0.48rem;
  background: #FCAF55;
  border-radius: 0.04rem;
  color: #fff;
  text-align: center;
  font-size: 0.16rem;
  display: inline-block;
  margin-bottom: 0.14rem;
  cursor: pointer;
}
.create-ip .total-box img {
  margin-right: 0.12rem;
}
.create-ip .total-box .price {
  color: #FCAF55;
  font-size: 0.14rem;
  line-height: 1;
}
.create-ip .total-box .price .num {
  font-size: 0.32rem;
}
.create-ip .total-box .content {
  width: 77.945619%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 0.2rem 0;
}
.create-ip .total-box .item {
  display: flex;
  font-size: 0.13rem;
  color: #9696A3;
}
.create-ip .total-box p {
  margin-bottom: 0;
}
.create-ip .total-box .config .ant-select {
  min-width: 1rem;
}
.create-ip .total-box .config .duration {
  position: relative;
}
.create-ip .total-box .l-config:first-child {
  margin-right: 0.4rem;
}
.create-ip .total-box .tit {
  font-size: 0.16rem;
  color: #040E34;
  font-weight: 400;
  margin-bottom: 0.2rem;
  line-height: 1;
}
.create-ip .total-box .des {
  line-height: 0.28rem;
  display: flex;
  align-items: center;
}
.create-ip .total-box .des img {
  border-radius: 0.04rem;
}
.create-ip .total-box .cycle {
  line-height: 0.32rem;
  height: 0.32rem;
  min-width: 1.5rem;
  width: auto;
}
.create-ip .total-box .ant-select-selector,
.create-ip .total-box .ant-select-selection-item {
  line-height: 0.32rem !important;
  height: 0.32rem !important;
}
.create-ip .total-box .ant-input-number {
  width: 0.8rem;
  line-height: 0.32rem;
}
.create-ip .create-btn {
  width: 1.6rem;
  line-height: 0.48rem;
  height: 0.48rem;
  border-radius: 0.04rem;
  background: #5C5DE3;
  color: #fff;
  text-align: center;
  font-size: 0.16rem;
  margin-top: 0.2rem;
  cursor: pointer;
}
